<template>
  <div id="intro" @touchstart="hideList">
    <div id="main">
      <p>关于我们</p>
      <p>公司简介，社会责任，管理层信息，网络安全，公司出版物等</p>
    </div>
    <div class="main-content">
      <div v-for="(item, index) in mainContent" v-bind:key="index" class="page" :style="renderPage(index)">
        <p>{{item.caption}}</p>
      </div>
      <foot></foot>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import foot from './Foot'
export default {
  name: 'intro',
  data () {
    return {
      mainContent: [
        {caption: '1'},
        {caption: '2'},
        {caption: '3'},
        {caption: '4'}
      ]
    }
  },
  methods: {
    hideList: function () {
      this.$root.eventHub.$emit('hide')
    },
    renderPage: function (i) {
      if (i % 2 === 0) {
        return 'background-color: white;'
      } else {
        return 'background-color: #F0F2F5;'
      }
    }
  },
  components: {
    foot
  },
  mounted () {
    if (!this.$root.adapt) {
      var temp = this.$root.carousel
      $('#main').css('height', temp)
      var text = $('#main p')
      var height = 0
      for (var i = 0; i < text.length; i++) {
        height += text.eq(i).height()
      }
      $('#main p:first-of-type').css('margin-top', (temp - height) / 2)
    }
  }
}
</script>

<style scoped>
  #main{
    background-size: cover;
    overflow: hidden;
    position: relative;
    background: #1296db;
  }
  #main p{
    text-align: center;
    color: white;
    line-height: 140%;
  }
  .main-content{
    width: 100%;
    height: auto;
  }

  @media only screen and (max-device-width: 767px) {
    #main {
      width: 100%;
      height: 350px;
    }
    #main p{
      line-height: 38px;
    }
    #main p:first-of-type{
      padding: 0 40px;
      font-size: 24px;
    }
    #main p:nth-of-type(2){
      padding: 0 30px;
      font-size: 22px;
    }
    .page{
      width: 100%;
      height: 330px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    #main p:first-of-type{
      margin-top: 210px;
      padding: 0 40px;
      font-size: 32px;
    }
    #main p:nth-of-type(2){
      margin-top: 24px;
      padding: 0 30px;
      font-size: 22px;
    }
    .page{
      width: 100%;
      height: 730px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-height: 960px) {
    #main p:first-of-type{
      margin-top: 240px;
    }
  }
</style>



